.container {
  margin-right: 24px;
  max-width: 800px;
  position: relative;
}

.container h1 {
  color: var(--colors-grey900);
  font-size: 24px;
  font-weight: 500;
}

.container h2 {
  font-size: 16px;
  margin: 20px 0 8px;
}

.container p {
  font-size: 14px;
  margin: 6px 0 12px;
}

.orgUnit {
  position: relative;
}

.orgUnit h1 svg {
  margin-bottom: -3px;
}

.message {
  color: #555;
  font-size: 14px;
  font-style: italic;
}
